	export default {
		data() {
			return {
				myUrl:'../../static/CTKY/unSeat.png',
                isSelect:false,
			}
		},
		onLoad(e) {
			uni.setNavigationBarTitle({
				title: '在线选座'
			});
			var that = this;
		},
		onReady() {

		},
		methods: {
			selectSeat() {
				if(this.isSelect){
					this.myUrl='../../static/CTKY/selectSeat.png';
					this.isSelect=!this.isSelect;
				}
				else{
					this.myUrl='../../static/CTKY/unSeat.png';
					this.isSelect=!this.isSelect;
				}
				
			}

		}
	}
